import React from 'react'
import $ from 'jquery'


function getData() {
	var a = atob(window.location.search.split("=")[1]).split("/"); 
	a = a[a.length-1];

	var settings = {
	  "url": window.location.origin+"/edi/about/"+a,
	  "method": "GET",
	  "timeout": 0,
	};

	$.ajax(settings).done(function (response) {
		console.log(response);
		response = response[0]
		var exclude = ["NAME","LOCATION", "WEBSITE", "SOURCE"]
		var lists = ["ORIENTATION", "POTENTIAL_CLASSES"]
		var data = ``
		for(var dat in response) {
			var val = response[dat]			
			if(!exclude.includes(dat) && val!="" && val!=null) {

				if(lists.includes(dat)) {
					val=val.split(", ")
					var temp = ``
					for(var v in val) {
						v = val[v]
						temp+=`<li>${v}</li>`
					}					 
					val = temp.length > 100? `<ul class="h-96 mx-10 overflow-scroll">${temp}<ul>`:`<ul class="mx-10">${temp}</ul>`

				}
				while(dat.includes("_")) {
					dat = dat.replace("_", " ")
				}
				dat = dat.toLowerCase()				
				dat = dat.split(" ")
				for(var d in dat) {
					var ind = d
					d = dat[d];
					var c = d[0].toUpperCase()
					var rem = d.substring(1)
					dat[ind] = c + rem				
				}
				dat = dat.join(" ")
				
				data+=`
				<div class="border-l hover:border-black m-2">
					<div class="m-2">
						<h3 class="text-lg tracking-wide">${dat}<h3>
						<p class="mx-10 tracking-tight">${val}</p>
					</div>
				</div>
				`
			}
		}
		$("#src").html(`${response.SOURCE}`)
		$("#src").attr("href", response.SOURCE)
		$("#profile").html(`
			<div class="mx-3">
				<div class="my-20">
					<h1 class="text-3xl tracking-widest w-full text-center">${response.NAME}</h1>	
					<h4 class="tracking-tighter text-center w-full text-md">${response.LOCATION}</h4>
					<center>
						<a class="rounded border border-black w-1/4 px-2" href="${response.WEBSITE}">Visit</a>
					</center>
				</div>
				<div class="text-md md:grid grid-cols-3 gap-4">
					${data}
				</div>
			</div>
			`)
	});
}



function about() {
	getData()
	return (
		<>
			<div class="border-b py-3" id="profile">			
			</div>
			<div class="w-full text-center text-blue-400 py-10"><a id="src"></a></div>
		</>
	)
}

export default about;
