import React, {Component} from 'react'
import $ from 'jquery'
import Card from '../components/CollegeCards.js'
var settings = {
  "url": window.location.origin+"/edi/get_all/"+window.location.search.split("=")[1],
  "method": "GET",
  "timeout": 0,
};


function cd(dat) {
	return (
		<Card data={dat} />
	)
}

const CARD = (data) => {
	var encoded = btoa(unescape(encodeURIComponent(data.WEBSITE)))
	return `
	<div class="border-l py-2 hover:border-black">	
		<div class="m-3 border-b">
			<a href="/about?q=${encoded}">
				<h2 class="text-2xl tracking-wide">${data.NAME}</h2>
				<h3 class="text-sm tracking-tighter">${data.LOCATION}</h3>			
			</a>
		</div>
		<div class="mx-4 tracking-tight">
			<p>Acceptance Rate: ${data.ACCEPTANCE_RATE}</p>
			<p>Type: ${data.TYPE}</p>			
		</div>
	</div>
		
	`
}


function query() {
	$("#list").html(``)
	var settings = {
	  "url": window.location.origin+"/edi/query/"+$("#query")[0].value,
	  "method": "GET",
	  "timeout": 0,
	};

	$.ajax(settings).done(function (response) {
		if(response.length == 0) {
			$("#list").append(`<div class="w-full text-center">We couldnt find anything</div>`)
		}
		for(var r in response) {			
			r = response[r]
			$("#list").append(CARD(r))
		
		}
	});


}


class allColleges extends Component {


	constructor() {
		super();
		this.state = {data: [{NAME:"nane"}]}

	}


	componentDidMount() {
		fetch(settings.url)
		.then(result => result.json())
		.then(dataO => {
			console.log(dataO);
			this.setState({data: dataO});			
			this.state.data.map(dat => {			
				$("#list").append(CARD(dat))
			})
		})		
		

	}

	render() {
		return (
			<>
				<div class="grid grid-cols-2">						
					<div>
						<div class="text-center text-2xl">Page {window.location.search.split("=")[1]}</div>			
						<div class="text-center"><a class="px-4 border-b hover:border-black" href={"/all?p="+(parseInt(window.location.search.split("=")[1])<1?parseInt(window.location.search.split("=")[1])-1:1)}>previous</a> <a class="px-4 border-b hover:border-black" href={"/all?p="+(parseInt(window.location.search.split("=")[1])+1)}>next</a></div>			
					</div>
					<div class="my-auto">
						<center>
							<input type="text" id="query" placeholder="Search for school..." class="border-b hover:border-black text-center focus:ring-4 focus:ring-black focus:ring-opacity-5"></input>
							<button id="searchButton" onClick={query}>Search</button>
						</center>
					</div>
				</div>
				<div id="list">		
				</div>

				<div class="text-center"><a class="px-4 border-b hover:border-black" href={"/all?p="+(parseInt(window.location.search.split("=")[1])<1?parseInt(window.location.search.split("=")[1])-1:1)}>previous</a> <a class="px-4 border-b hover:border-black" href={"/all?p="+(parseInt(window.location.search.split("=")[1])+1)}>next</a></div>
			</>
		)
	}

}

export default allColleges;
