import React from 'react';
import $ from 'jquery'

var filter = {}

function addParam() {	
	var param = $("#param")[0].value,
	    val = $("#value")[0].value
	if(param!="def"){
		filter[param] = val
		$("#flt").html(``)
		for(var f in filter) {			
			$("#flt").append(`<li>${f}: ${filter[f]}</li>`)
		}
	}
	console.log(filter)

}

var settings = {
  "url": window.location.origin+"/edi/describe",
  "method": "GET",
  "timeout": 0,
};

$.ajax(settings).done(function (response) {
	console.log(response);
	response = response.values
	for(var a in response) {
		var dat = response[a]


		while(dat.includes("_")) {
			dat = dat.replace("_", " ")
		}
		dat = dat.toLowerCase()				
		dat = dat.split(" ")
		for(var d in dat) {
			var ind = d
			d = dat[d];
			var c = d[0].toUpperCase()
			var rem = d.substring(1)
			dat[ind] = c + rem				
		}
		dat = dat.join(" ")
		$("#param").append(`<option value="${response[a]}">${dat}</option>`)
	}
});

function applyFilter() {
$("#res").html(``)
var settings = {
  "url": window.location.origin+"/edi/filter",
  "method": "POST",
  "timeout": 0,
  "headers": {
    "Content-Type": "application/json"
  },
  "data": JSON.stringify(filter),
};

$.ajax(settings).done(function (response) {
	console.log(response);
	$("#res").append(`<a>results: ${response.found}</a>`)
	response = response.results
	for(var r in response) {

		var link = btoa(unescape(response[r].WEBSITE))
		$("#res").append(`
			<div class="my-auto text-center tracking-wide border-l border-r hover:border-black">
				<a onclick="window.open('/about?q=${link}')">${response[r].NAME}</a>
			</div>
			`)
	}
});



}

function Filter() {
	return (
		<div>
			<div>
				<center>
					<div class="py-10">
						<h1 class="tracking-widest text-2xl py-5">Filtering</h1>
						<lable for="param">Select parameter by which to filter: </lable>
						<select id="param" class="mx-3 rounded border-b hover:border-black"><option value="def">- select -</option></select>
						<br></br>
						<lable for="value">Enter value to filter for</lable>	
						<input id="value" class="border-b border-black mx-2 text-center" placeholder="value..."></input>						
						<button onClick={addParam} class="rounded border border-black px-2 mx-2">Add to filter</button>					
						<ul id="flt">
						</ul>
						<div>
							<button onClick={applyFilter} class="rounded border border-black p-2 my-2">Apply filter</button>
						</div>
						<div id="res" class="grid grid-cols-2  md:grid-cols-4 gap-4">
						
						</div>
						
					</div>
					
				</center>
			
			
			</div>
		</div>
	)

}
export default Filter;
